@import url(https://fonts.googleapis.com/css2?family=Itim&display=swap);
body{margin:0;font-family:"Itim",cursive;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
.navContainer{position:fixed;top:0;height:var(--nav-height);width:100%;overflow:hidden}.navContainer .appBar{background:var(--color-brand)}.navContainer .appBar .typo{flex-grow:1;margin:0}.navContainer .appBar .iconButton{color:#fff}@media(max-width: 900px){.navContainer .toolBar .typo.hiddenTypo{display:none}.navContainer .toolBar .searchBarContainer.hidden{visibility:visible;flex-grow:1}.navContainer .toolBar .searchBarContainer.visible{display:none}}
.searchBarContainer .inputBase{background:#fff;margin-left:30px;border-radius:5px}@media(max-width: 900px){.searchBarContainer.hidden{visibility:hidden}.searchBarContainer .iconButton{background:var(--color-brand);color:#fff}}
.cardContainer{max-width:100%;height:400px;margin:10px;border:solid 1px var(--color-gray);border-radius:20px;overflow:hidden;animation:1s ease-out 0s 1 showUp}.cardContainer .imageContainer{position:relative;height:50%;width:100%;overflow:hidden}.cardContainer .imageContainer .frontImage{position:absolute;height:100%;width:100%;z-index:1}.cardContainer .imageContainer .frontImage img{max-height:100%}.cardContainer .imageContainer .backImage{position:absolute;width:100%;min-height:100%;-webkit-filter:blur(8px);filter:blur(8px)}.cardContainer .imageContainer .backImage img{max-width:100%}.cardContainer .infoContainer{height:38%;width:96%;margin:1% 2%}.cardContainer .infoContainer h3{color:var(--color-brand);margin-top:0}.cardContainer .infoContainer .info{width:100%;height:50%}.cardContainer .infoContainer .info p{align-content:center}.cardContainer .infoContainer .info p span{color:var(--color-brand)}.cardContainer .infoContainer .info.inactive{display:none}.cardContainer .formContainer{height:10%;display:flex;justify-content:center;align-items:center}.cardContainer .formContainer .paw{margin:0}@keyframes showUp{0%{opacity:0}100%{opacity:1}}
.catalogueContainer{display:flex}.catalogueContainer .catalogueScreen{width:100%}.catalogueContainer .catalogueScreen .searchBarContainer{display:none}.catalogueContainer .catalogueScreen .catalogueMatrix{display:grid;grid-auto-rows:420px;grid-template-columns:16.6% 16.6% 16.6% 16.6% 16.6% 16.6%}.catalogueContainer .catalogueScreen .loadButton{background:var(--color-brand);color:#fff;font-family:"Itim",cursive;animation:1s ease-out 0s 1 showUp}.catalogueContainer .defaultResponse{margin-left:auto;margin-right:auto;margin-top:50px;margin-bottom:50px;border:3px solid var(--color-brand);height:200px;width:200px;border-radius:50%;animation:1s ease-out 0s 1 showUp}@keyframes showUp{0%{opacity:0}100%{opacity:1}}@media(max-width: 1600px){.catalogueContainer .catalogueScreen .catalogueMatrix{grid-template-columns:25% 25% 25% 25%}}@media(max-width: 1200px){.catalogueContainer .catalogueScreen .catalogueMatrix{grid-template-columns:33.3% 33.3% 33.3%}}@media(max-width: 900px){.catalogueContainer .catalogueScreen .searchBarContainer{display:block}.catalogueContainer .catalogueScreen .catalogueMatrix{grid-template-columns:50% 50%}}@media(max-width: 600px){.catalogueContainer .catalogueScreen .catalogueMatrix{grid-template-columns:100%}}
.App{text-align:center;position:relative}.App .navContainer{position:fixed;top:0;z-index:10}.App .catalogueContainer{margin-top:56px;margin-top:var(--nav-height);padding-top:10px}:root{--color-brand: #ff452b;--color-light: #f8f8f8;--color-gray: #e0e0e0;--color-dark: #afafaf;--nav-height: 56px}
